#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding: 3rem;
    margin-bottom: 1.5rem;
}
.skillDesc{
    font-weight: 400;
    font-size: 1rem;
    max-width: 50rem;
    padding: 1rem 2rem;
}
.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.skillBar{
    display: flex;
    /* flex-wrap: wrap; */
    /* overflow:auto; */
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50,50,50);
}
.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}

.skillBarText>p{
        font-size: 0.9rem;
        font-weight: 200;
}
@media screen and (max-width:480px){
    .skillBar{
        flex-wrap: wrap;
    }
    .skillBarText>p{
        font-size:2vw;
    }
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
        margin-right: 5 rem;
    }
    .skillBarText>h2{
        font-size: 5vw;
    }
}
